.subscribe {
  padding-top: 60px;

  background-color: $blue;

  color: #ffffff;

  &__count {
    text-transform: uppercase;
    font-size: $mobileTextFS;
    letter-spacing: $mobileTextLS;
  }

  &__title {
    margin-top: 20px;
  }

  &__form {
    margin-top: 30px;
  }
}

@media(min-width: $desktopWidth) {
  .subscribe {
    padding-top: 60px;
    background-color: $blue;
    color: #ffffff;

    &__container {
      max-width: 510px;
    }

    &__count {
      text-transform: uppercase;
      font-size: 15px;
      letter-spacing: $desktopTextLS;
    }

    &__title {
      margin-top: 20px;
    }

    &__form {
      margin-top: 30px;
    }
  }
}
