.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;

  position: absolute;
  left: -100%;
  transition: 1s all;

  &__list {
    margin-top: 0;
    margin-left: 0;
    margin-bottom: 0;
    padding-left: 0;
    list-style: none;
  }

  &__item {
    &--link {
      text-decoration: none;
      text-transform: uppercase;
      color: #ffffff;
      font-size: $mobileTextFS;
      font-weight: 400;
      letter-spacing: 2px;
    }
  }

  &__button {
    margin-top: 20px;
    display: none;
  }
}

.header {
  .nav {
    &__list {
      display: none;
    }

    &__social {
      margin-top: 200px;
      display: none;
    }
  }
}

.nav--active {
  left: 0;

  .nav {
    &__list {
      display: block;
      width: 100%;

    }

    &__item {
      padding: 20px 0;
      border-top: 0.5px solid #444A6A;

      &:last-child {
        border-bottom: 0.5px solid #444A6A;
      }
    }

    &__button {
      display: block;
    }

    &__social {
      display: flex;
    }
  }
}

.footer {
  .nav {
    position: static;
    display: flex;

    &__list {
      display: flex;
      flex-direction: column;
      align-items: center;
      flex-wrap: wrap;
    }

    &__item {
      margin-bottom: 30px;

      &--link {
        color: #ffffff;
        font-weight: 400;
        font-size: 15px;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__social {
      margin-top: 30px;
    }

  }
}


@media(min-width: $desktopWidth) {
  .nav {
    position: static;
    width: auto;
    display: flex;
    flex-direction: row;
    align-items: center;

    &__list {
      list-style: none;
      flex-direction: row;
      justify-content: space-between;
    }

    &__item {
      margin-right: 30px;

      &:last-child {
        margin-right: 0;
      }

      &--link {
        padding: 10px;
        text-decoration: none;
        text-transform: uppercase;
        color: $main;
        font-size: 16px;
        font-weight: 400;

        &:hover {
          color: $red;
        }
      }
    }

    &__button {
      margin-top: 0;
      margin-left: 40px;
      display: block;
    }

  }

  .header {
    .nav {
      &__list {
        display: flex;
      }
    }
  }

  .footer {
    .nav {
      align-items: center;
      justify-content: space-between;
      width: 100%;
      padding-left: 60px;

      &__button {
        display: none;
      }

      &__list {
        display: flex;
        flex-direction: row;
        align-items: center;
        flex-wrap: wrap;
      }

      &__item {
        margin-bottom: 0;

        &:last-child {
          margin-bottom: 0;
        }
      }

      &__social {
        margin-top: 0;
      }
    }
  }
}
